@import "https://fonts.googleapis.com/css2?family=Pacifico&display=swap";
body {
  text-align: center;
  background-color: tomato;
  margin: auto;
  transition: color 2s ease-in-out;
}

body.final {
  background-color: #55e7f4;
}

.main-container {
  height: 700px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 50px;
  display: flex;
  overflow: hidden;
}

@media only screen and (min-width: 600px) {
  .main-container {
    height: auto;
  }
}

h1 {
  color: #fff;
  text-shadow: 1px 1px 2px #000;
  height: 80px;
  white-space: nowrap;
  margin: 0;
  padding: 0;
  font-family: Pacifico, cursive;
  font-size: 30px;
  line-height: 1.2;
  display: block;
}

@media only screen and (min-width: 600px) {
  h1 {
    font-size: 55px;
  }
}

h1 > span {
  transition: all 2s ease-in-out;
  position: relative;
  top: 0;
  left: 0;
}

h1 > span.or {
  font-size: 35px;
  position: relative;
  top: 30px;
}

body.final h1 .boy {
  top: calc(50vh - 60px);
  left: calc(50vw - 100px);
}

@media only screen and (min-width: 600px) {
  body.final h1 .boy {
    left: 10vw;
  }
}

body.final h1 .or, body.final h1 .girl {
  opacity: 0;
  left: 200px;
}

.luckywheel-container {
  max-width: 550px;
  min-width: 100px;
  width: 100%;
  position: relative;
  top: -50px;
}

@media only screen and (min-width: 600px) {
  .luckywheel-container {
    width: 50%;
    top: 0;
  }
}

.sectors .sector:nth-child(2n) {
  fill: #55e7f4;
}

.sectors .sector:nth-child(2n+1) {
  fill: #ff66ac;
  transition: all .5s ease-in-out;
}

body.final .sectors .sector:nth-child(2n+1) {
  fill: #55e7f4;
}

.frame, .sticks, .winIndicator, .wheelMiddle {
  fill: #fff;
}

.luckywheel {
  width: 85%;
  margin: 40px auto;
  transition: all 2s ease-in-out;
  position: relative;
  top: 0;
}

body.final .luckywheel {
  opacity: 0;
  top: 300px;
}

.button-container {
  position: relative;
  top: -130px;
}

@media only screen and (min-width: 600px) {
  .button-container {
    top: -20px;
  }
}

.button-28 {
  appearance: none;
  box-sizing: border-box;
  color: #3b3b3b;
  cursor: pointer;
  min-height: 60px;
  min-width: 0;
  text-align: center;
  -webkit-user-select: none;
  user-select: none;
  touch-action: manipulation;
  width: 100%;
  will-change: transform;
  background-color: #fff;
  border: 2px solid #1a1a1a;
  border-radius: 15px;
  outline: none;
  margin: 0;
  padding: 16px 24px;
  font-family: Roobert, -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: 16px;
  font-weight: 600;
  line-height: normal;
  text-decoration: none;
  transition: all .3s cubic-bezier(.23, 1, .32, 1);
  display: inline-block;
}

.button-28.hide {
  opacity: 0;
}

.button-28:disabled {
  pointer-events: none;
}

.button-28:hover {
  color: #fff;
  background-color: #1a1a1a;
  transform: translateY(-2px);
  box-shadow: 0 8px 15px #00000040;
}

.button-28:active {
  box-shadow: none;
  transform: translateY(0);
}

#confetti {
  display: none;
}

body.final #confetti {
  height: 100%;
  width: 100%;
  z-index: -1;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

